html {
  height: 100%;
}

body {
  width: 100%;
  margin: 0 auto;
  font-family: $Stratos;
  font-size: 16px;
  line-height: 150%;
  color: $black;
  font-style: normal;
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  overflow-x: hidden;
}

.no-scroll {
  overflow-y: hidden;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  overflow: hidden;
  white-space: nowrap;
  clip-path: inset(100%);
}
