.ie-banner-overlay {
  position: fixed;
  z-index: 10000;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: $gray-overlay;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: auto;

  &--hide {
    display: none;
  }
}

.ie-banner {
  position: relative;
  background-color: $white;
  border-radius: 8px;
  width: 1006px;
  height: auto;
  display: flex;

  &__pic {
    min-width: 41%;

    img {
      width: auto;
      height: 100%;
      border-radius: 8px 0 0 8px;
    }
  }

  &__description {
    min-width: 59%;
    box-sizing: border-box;
    padding: 48px 48px 56px 52px;

    h1 {
      font-size: 2em;
      font-weight: normal;
      line-height: 120%;
      letter-spacing: 0.03em;
      color: #000078;
      margin: 0;
    }

    p,
    span {
      font-family: $Inter;
      letter-spacing: 0.03em;
    }
  }

  &__links {
    margin: 24px 0 0;
  }

  &__links-list {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
    list-style: none;
    margin: 0;
    padding: 0;
  }

  &__links-item {
    width: 32%;

    img {
      width: 32px;
      height: 32px;
      margin: 0 16px 0 0;
    }

    span {
      text-transform: uppercase;
      color: $blue;
      font-weight: bold;
    }

    &--qr {
      box-sizing: border-box;
      background-color: $gray-background;
      border-radius: 8px;
      padding: 16px;
      display: flex;
      align-items: center;
      width: 100%;
      margin: 12px 0 0;

      img {
        width: 134px;
        height: 134px;
        margin: 0 16px 0 0;
      }
    }
  }

  &__browser-link {
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    text-decoration: none;
    box-sizing: border-box;
    background-color: $gray-background;
    border-radius: 8px;
    padding: 16px;

    &:hover {
      span {
        color: $blue-hover;
      }
    }
  }

  &__button {
    width: 100%;
    box-sizing: border-box;
    background-color: $blue;
    color: $white;
    font-family: "Arial", sans-serif;
    font-weight: bold;
    font-size: 14px;
    line-height: 120%;
    text-transform: uppercase;
    border: none;
    outline: none;
    padding: 20px;
    margin: 40px 0 0;
    border-radius: 8px;
    cursor: pointer;

    &:hover {
      background-color: $blue-hover;
      box-shadow: 0 10px 16px rgba(45, 128, 252, 0.2);
    }
  }
}
